import React from 'react'

class DragAndDrop extends React.Component {
    constructor(props){
        super(props)
    
        this.ballRef = React.createRef()
        this.handeleDown = this.handeleDown.bind(this)
    }
    handeleDown(e){
        this.ballRef.current.onmousedown = () =>{

            this.ballRef.current.style.position = 'absolute'
            this.ballRef.current.style.zIndex = 1;

            document.body.appendChild(this.ballRef.current)

            document.onmousemove = (e) => {
                this.moveAt(e)
            }

            this.ballRef.current.onmouseup = () => {
                document.onmousemove = null;
                this.ballRef.current.onmouseup = null;
            }

            this.ballRef.current.ondragstart = function() {
                return false;
            };
        }
    }
    moveAt(e){
        this.ballRef.current.style.left = e.pageX - this.ballRef.current.offsetWidth /2 + 'px'
        this.ballRef.current.style.top = e.pageY - this.ballRef.current.offsetHeight /2 + 'px'
    }
    render(){
        const {stiker, sizeStiker} = this.props
        return (
            <img ref={this.ballRef}
                onMouseDown = {this.handeleDown}
                src={stiker.thumb_photo} alt="" width={sizeStiker.inputValueLong+4} height={sizeStiker.inputValueHeight+4}
            />
        )
    }

}

export default DragAndDrop