import React from 'react';
import { Panel, PanelHeader, Header, Group,  Div, FormItem, Input, Checkbox, Select, CustomSelectOption, Avatar} from '@vkontakte/vkui';
import '../css/main.css'
import DragAndDrop from './Dad';


class Home extends React.Component {
	constructor(props){
		super(props)
		this.state = {
			inputValueLong: 6,
			inputValueHeight: 6,
			checked: false,
			isLoad: false,
			items: [],
			itemsOption: 0,
			stiker: {}
		}

		this.itemsMarket()
	}
	
	itemsMarket(){
		const {items} = this.props
		items.then(items => this.setState({
			items: items,
			isLoad: true
		}))
	}

	calculator(){
		let pochta = 0

		if(this.state.inputValueLong < 6 ) return ('Неверное значение')
		if(this.state.inputValueHeight < 6) return ('Неверное значение')
		if(this.state.checked) pochta = 100

		const rezSumm = (this.state.inputValueLong + this.state.inputValueHeight) * 4

		return (rezSumm + pochta)
	}
	renderValue (value){

		if(value < 0 ) value  = 0 - value + value
		return value

 	}
	handlerStiker(){
		let stiker =  this.state.items.filter(res => parseInt(res.id) === parseInt(this.state.itemsOption))

		if (stiker.length > 0 )
		return (
			<DragAndDrop stiker = {stiker[0]} sizeStiker = {{
				inputValueLong: this.state.inputValueLong,
				inputValueHeight: this.state.inputValueHeight
			}}/>
		)
	}
	render(){
		return (
			<Panel style= {{height: '400px'}}>
				<PanelHeader style= {{textAlign: 'center'}}>Калькулятор</PanelHeader>
		
				<Group header={<Header mode="secondary">Расчет стоимоти наклеек</Header>} style={{width: '600px', height: '500px'}}>
					<Div className = 'information'>
						<FormItem top ='Длина наклейки (СМ)'>
							<Input type = 'number' style= {{ width: '200px'}} value = {this.renderValue(this.state.inputValueLong)} onChange = {e =>this.setState({inputValueLong : parseInt(e.target.value)})} />
						</FormItem>
						<FormItem top ='Ширина наклейки (СМ)' style= {{display: 'block'}} >
							<Input type = 'number'  style= {{ width: '200px' }} value= {this.renderValue(this.state.inputValueHeight)} onChange = {e => this.setState({inputValueHeight: parseInt(e.target.value)})}/>
						</FormItem>
						<FormItem top = 'Доставка'>
							<Checkbox checked = {this.state.checked} onChange = {()=> this.setState({checked: !this.state.checked})}>Доставка почтой</Checkbox>
						</FormItem>
					</Div>
					<Div className = 'ilustrat'>
						<Select
							placeholder="Не выбран" 
							options={this.state.items.map(items => ({ label: items.title, value: items.id, avatar: items.thumb_photo }))}
							renderOption={({ option, ...restProps }) => (
								<CustomSelectOption {...restProps} before={<Avatar size={24} src={option.avatar} />} />
							)}
							onChange = {e => this.setState({itemsOption: e.target.value})}
						/>
						
					<div className = 'primire'>
						{this.handlerStiker()}	
					</div>

					<div className = "textsumm"><h1>Сумма: {this.calculator()} руб.</h1></div>
					</Div>	
				</Group>
			</Panel>
		)
	}
}

export default Home;
